    position: static;
    z-index: auto;
    margin: 0;
    padding: 0;
    list-style: none
}

.carousel-custom .carousel-indicators li {
    transform: none;
    opacity: 1
}

.carousel-custom .carousel-indicators li.active {
    transform: none;
    opacity: 1
}

