.ribbon .ribbon-label {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px 10px;
    position: absolute;
    z-index: 1;
    background-color: var(--kt-ribbon-label-bg);
    box-shadow: var(--kt-ribbon-label-box-shadow);
